.nav-pills-neutral {
  .nav-item {
    .nav-link {
      background-color: $opacity-2;
      color: $white;

      &.active {
        &,
        &:focus,
        &:hover {
          background-color: $white;
          color: $primary;
        }
      }
    }
  }
}

.nav-pills-primary {
  .nav-item {
    .nav-link.active {
      &,
      &:focus,
      &:hover {
        @include diagonal-gradient($primary, $primary-states);
      }
    }
  }
}

.nav-pills-info {
  .nav-item {
