.section {
  padding: 70px 0;
  position: relative;

  .row + .category {
    margin-top: $margin-base-vertical;
  }

  .carousel {
    width: 100%;
  }
}

.path {
  position: absolute;
  opacity: 0.02;
}

.dots {
  position: absolute;
  opacity: 0.07;
  z-index: 2;
}

.shapes {
  position: absolute;
  opacity: 0.02;
  z-index: 2;
}

.main {
  position: relative;
  width: $full-width;
}

.section-navbars {
  padding-bottom: 0;
  overflow: hidden;
  .navbar {
    z-index: 2;
  }

  img.path {
    left: -400px;
  }
}

.section-typo {
  overflow: hidden;
  .path {
    right: -400px;
  }

  .path1 {
    bottom: 5%;
    width: 70%;
    left: -200px;
  }
}

.section-pagination {
  .path {
    width: 65%;
    right: 0%;
    top: 48%;
  }

  .path1 {
    width: 30%;
    top: 0;
    left: 3%;
  }
}

.section-javascript {
  overflow: hidden;
  .path {
    right: -300px;
    width: 80%;
  }

  .path1 {
    width: 25%;
    left: 3%;
    top: 0;
  }
}

.section-full-screen {
