    width: 100px;
    background: url("/assets/img/square2.png");
    background-size: cover;
    top: 300px;
    left: 700px;
    animation-duration: 5s;
  }

  .square-4 {
    height: 250px;
    width: 250px;
    background: url("/assets/img/square3.png");
    background-size: cover;
